html,
body {
  width: 100%;
  margin: 0;
  padding: 0;
  background-color: #ffffff;
  color: #ffffff;
  font-family: "raleway";
}

* {
  box-sizing: border-box;
  scroll-behavior: smooth;
  -webkit-user-select: none;
  -moz-user-select: -moz-none;
  -o-user-select: none;
  user-select: none;
}

body::-webkit-scrollbar {
  width: 0.6em;
}

body::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

body::-webkit-scrollbar-thumb {
  background-color: #e31b6d;
  outline: 3px solid #e31b6d;
  border-radius: 20px;
}

.sky {
  background-image: linear-gradient(
    to right top,
    #101224,
    #15182c,
    #1b1e35,
    #22253e,
    #282b47
  );
  /* #080A1A      #282B47       */
}

span {
  position: absolute;
  background: linear-gradient(0deg, #a5a5a5 0%, transparent);
  height: 300px;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  animation: starDrop 5s infinite linear;
  filter: blur(0.8px);
}

span:nth-child(4n + 1) {
  background: linear-gradient(0deg, #bffdf9 0%, transparent);
}

span:nth-child(4n + 2) {
  background: linear-gradient(0deg, #a1abbb 0%, transparent);
}

span:nth-child(4n + 3) {
  background-color: rgb(147, 176, 180);
  background: linear-gradient(0deg, rgb(74, 74, 78), transparent);
}

@keyframes starDrop {
  0% {
    transform: rotate(45deg) translate(0px, -2000px);
  }

  100% {
    transform: rotate(45deg) translate(0px, calc(100vh + 200px));
  }
}

j {
  display: inline-block;
  position: absolute;
  background-color: #e31b6d;
  border-radius: 50%;
  animation: starMove 1s linear infinite;
}

j:nth-child(3n + 1) {
  background-color: #0078f0;
}

j:nth-child(3n + 2) {
  background-color: rgb(207, 199, 253);
}

@keyframes starMove {
  0% {
    transform: translateX(-100vw);
  }

  100% {
    transform: translateX(100vw);
  }
}

.css-typing h1 {
  border: none;
  border-right: 0.15em solid #0078f0;
  white-space: nowrap;
  overflow: hidden;
}

.css-typing h1:nth-child(1) {
  width: 10em;
  animation: type 5s steps(40, end);
  animation-fill-mode: forwards;
}

.css-typing h1:nth-child(2) {
  width: 16.5em;
  opacity: 0;
  animation: type 7s steps(40, end);
  animation-delay: 5s;
  animation-fill-mode: forwards;
}

.css-typing h1:nth-child(3) {
  width: 16.5em;
  opacity: 0;
  animation: type 7s steps(40, end);
  animation-delay: 13s;
  animation-fill-mode: forwards;
}

@keyframes type {
  0% {
    width: 0;
  }

  1% {
    opacity: 1;
  }

  99.9% {
    border-right: 0.15em solid #0078f0;
  }

  100% {
    opacity: 1;
    border: none;
  }
}

@keyframes blink {
  50% {
    border-color: transparent;
  }
}

@media screen and (max-width: 600px) {
  .css-typing h1 {
    overflow: hidden;
    border-right: 0.08em solid #0078f0;
    white-space: nowrap;
    margin: 0 auto;
    animation: typing 5s steps(40, end), blink 0.75s step-end infinite;
    animation-iteration-count: infinite;
    font-size: 20px;
    letter-spacing: 0.3em;
  }

  @keyframes typing {
    from {
      width: 0;
    }

    to {
      width: 15em;
    }
  }

  @keyframes blink {
    from,
    to {
      border-color: transparent;
    }

    50% {
      border-color: orange;
    }
  }
}

hr {
  margin: 50px 0;
  color: rgb(222, 216, 216);
}

.text-truncate-2 {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

@keyframes load {
  from {
    width: 0%;
  }
}

.bar {
  height: 15px;
  width: 100%;
  background-color: #eee;
  border-radius: 15px;
  margin-bottom: 5px;
  font-size: 14px;
  color: #fff;
  font-weight: bold;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.5);
}

.bar::before {
  content: "";
  height: 15px;
  background-color: #e31b6d;
  display: inline-block;
  padding: 5px 0 5px 10px;
  border-radius: inherit;
  animation: load 2s 0s;
  -webkit-animation: load 2s 0s;
  -moz-animation: load 2s 0s;
  -o-animation: load 2s 0s;
}

.bar.front::before {
  background-color: #ffcc33;
}

.bar.back::before {
  background-color: #a6cfe3;
}

.bar.learning::before {
  width: calc(30% - 10px);
}

.bar.basic::before {
  width: calc(40% - 10px);
}

.bar.intermediate::before {
  width: calc(60% - 10px);
}

.bar.advanced::before {
  width: calc(80% - 20px);
}

.bar.expert::before {
  width: calc(100% - 30px);
}

.hover\:text-pink:hover {
  --tw-text-opacity: 1;
  color: rgb(227 27 109 / var(--tw-text-opacity));
}

